import React from 'react';
import {Route, Switch, Redirect} from 'react-router-dom';
import Home from './components/pages/home/Home';
import HomeVisitor from './components/pages/home/HomeVisitor';
import GuidePage from './components/pages/gym/GuidePage';
import Exercice from './components/pages/gym/Exercice';
import Signin from './components/pages/signin/Signin';
import Profil from './components/pages/profil/Profil';
import Contact from './components/pages/contact/Contact';
import DashboardAdmin from './components/pages/dashboardAdmin/DashboardAdmin';
import DashboardMachines from './components/pages/dashboardMachines/DashboardMachines';

class Routes extends React.Component {

 
 render() {


    
                
   

    
      

            return (

                    <Switch> 
                        
                        {sessionStorage.isAdmin == 0 ? (
                            <div> 
                            <Route path="/" exact strict component={Home} />
                            <Route path="/GuidePage" component={GuidePage} />
                            <Route path="/exercice" component={Exercice} />
                            <Route path="/profil" component={Profil} /> 
                            <Route path="/contact" component={Contact} />
                            </div> ) : 
                        sessionStorage.isAdmin == 1 ? 
                            <div>
                            <Route path="/" exact strict component={Home} />
                            <Route path="/GuidePage" component={GuidePage} />
                            <Route path="/exercice" component={Exercice} />
                            <Route path='/profil' component={Profil} /> 
                            <Route path="/contact" component={Contact} />
                            <Route path="/DashboardAdmin" component={DashboardAdmin} /> 
                            <Route path="/DashboardMachines" component={DashboardMachines} />
                            </div> : 
                            
                        (<div>
                            <Route path="/home" exact strict  component={HomeVisitor} />
                            <Route path="/Signin" component={Signin} />
                            <Route path="/contact" component={Contact} />

                                                        
                            { sessionStorage.id === 0  ? (
                             <Route path='/profil' exact strict component={Profil} /> ):
                                ( <Redirect to="/home" /> ) }
        
    
                          </div>) 
                                
                        }
                        
                    </Switch>

        );

    }

}
export default Routes;


