import React from 'react';
import '../../../reset.css';
import '../../../index.css';
import './register.css';
import axios from 'axios';
import MaterialIcon from 'material-icons-react';


class Register extends React.Component {

  constructor() {
    super();
    this.state = {
      pseudo : '',
      email : '',
      password : '',
      passwordConfirm : '',
      isPasswordConfirm : true,
      message : null,
      majValidation : null,
      charValidation : null,
      lengthValidation : null,
      passStrong : false,


    };

   this.handleInputChange = this.handleInputChange.bind(this);
   this.handleSubmit = this.handleSubmit.bind(this);
   this.confirmPassword = this.confirmPassword.bind(this);
   this.isPasswordStrong = this.isPasswordStrong.bind(this);
   this.validEmail = this.validEmail.bind(this);
   this.checkPassword = this.checkPassword.bind(this);


  }

  test() {
    console.log("test export");
  }

  

  handleInputChange(e) {
  this.setState({[e.target.name]: e.target.value}, 
     () => { 
      this.checkPassword();  //la fonction checkPassword est un callback de handlechange 

    });
    console.log(this.state.pseudo);
    }

    validEmail(email) {

      var mailRegex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return mailRegex.test(email);
    }

    checkPassword() {
      this.isPasswordStrong();
      this.confirmPassword();

      return this.isPasswordStrong && this.confirmPassword;
    }
    
    isPasswordStrong() {
      // test majuscule
      var majRegex = new RegExp("^(?=.*[A-Z])");
      var majTest = majRegex.test(this.state.password);
      // test charactere speciaux 
      var specialRegex = new RegExp("^(?=.*[!@#\$%\^&\*\?])");
      var specialTest = specialRegex.test(this.state.password);
     // test mdp contient 8 exact characteres 
     var lengthTest = this.state.password.length === 8;
    
      if(majTest) {
        this.setState({
          majValidation : "validate",
        });
      }else {
        this.setState({
          majValidation : null,
        })
      }
      if (specialTest){
          this.setState({
            charValidation : "validate",
          });
      }else {
          this.setState({
            charValidation : null,
          })
        }
      if(lengthTest) {
          this.setState({
              lengthValidation : "validate",
          })
      }else {
          this.setState({
              lengthValidation : null,
          })
      }
        return  majTest && specialTest && lengthTest;
 }

  confirmPassword() {
      this.setState({
        isPasswordConfirm :  this.state.password === this.state.passwordConfirm

      }) 
      
      return this.state.isPasswordConfirm;
    }

    
   
     
  

  handleSubmit(e) {
    const isEmailValid = this.validEmail(this.state.email);
    const passwordStrong = this.isPasswordStrong();
    const passwordConfirm = this.confirmPassword();
    console.log("check mail et pass" , isEmailValid && passwordStrong &&passwordConfirm);

    const user = {
      pseudo : this.state.pseudo,
      email : this.state.email,
      password : this.state.password
    } 
  e.preventDefault(); // emêche le comportement d'un formulaire si le mdp n'est pas confirmé et si password not strong 
    //console.log("mail et pass", isEmailValid && checkpassword);
    if(isEmailValid && passwordStrong &&passwordConfirm) {


    axios
    .post(`http://localhost:3002/register`, {user})
    .then(response => {
      this.setState({
        message : response.data
      })
      
    })
    .catch(err => {
      console.log("data not send", err.response); 
    })
    e.preventDefault();
  }
  else {
    e.preventDefault(); // emêche le comportement d'un formulaire si le mdp n'est pas confirmé et si password not strong 
    console.log("le mdp n'est pas correcte ! ")
    this.setState({
      message : "Mot de passe ou mail incorrect"
    })

  }
}

  render() {

  const  stylecss2 = {
    visibility :  this.props.stylecss,
  }
  console.log(stylecss2);
  console.log("is password confirm" , this.state.isPasswordConfirm);

  const isPasswordConfirm = this.state.isPasswordConfirm;


  





    return (
      
    <div  id="openModal" className="modalDialog"  style={stylecss2} stylecss='hidden'>
        <div className="modal">
          <a className="iconclear" title="Fermer" onClick={this.props.close}   >   <MaterialIcon icon="clear" /> </a>
          
          <div   id="register">
            <form className="formlogin" onSubmit={this.handleSubmit}> 
                <input onChange={this.handleInputChange} value={this.state.pseudo} className="inputregister" name="pseudo" type="text" placeholder="Pseudo" required />
                <input onChange={this.handleInputChange} value={this.state.email} className="inputregister"  name="email" type="email" placeholder="E-mail" required/> 
                <input  onChange={this.handleInputChange}  className="inputregister"  name="password"  type="password" placeholder="mot de passe " required/>
                <input onChange={this.handleInputChange}  className="inputregister" name="passwordConfirm"type="password" placeholder="confirmer le mot de passe" required/>
                {!isPasswordConfirm ? (<span className="confirm">Please confirm your password</span>) : <span></span>}
                

                <input className="button" type="submit" value="S'INSCRIRE"/> 
                <ul id="passwordcontraints">
                    <li className={this.state.lengthValidation}> 8 caractères </li> 
                    <li className={this.state.majValidation}> 1 majuscule </li> 
                    <li className={this.state.charValidation}> 1 caractère spécial </li> 
                </ul>
                <p> {this.state.message} </p>
                <a onClick={this.props.signin}> Déjà un compte? </a>

            </form>

          </div>
    </div>
  </div>

    );
  }


}



export {test};


export default Register;







