import React from 'react';
import '../../reset.css';
import './footer.css';
import {Link, Redirect} from 'react-router-dom';



class Footer extends React.Component {
    render() {
      return (
        <footer className="footer">
            <nav> 
                <ul className="navfooter">
                    <a to="/contact" className="itemfooter" > A propos</a>
                    <Link to ="/contact" className="itemfooter"> Contact </Link> 
                    <li className="itemfooter"> Mention légale </li>
                </ul> 
            </nav>
        </footer>
    );
    }
  }
  
export default Footer;


