import React from 'react';
import '../../../reset.css';
import '../../../index.css';
import './dashboardAdmin.css';

import axios from 'axios';


class DashboardAdmin extends React.Component {

constructor() {
    super();
    this.state = {
        users : [],
        clickedUser : null,
        UpdateClicked : false,
        message : '',
        isAdmin : null,
    }
    
    this.getUsers = this.getUsers.bind(this);
    this.deleteUser = this.deleteUser.bind(this);
    this.selectUser = this.selectUser.bind(this);
    this.updateUser = this.updateUser.bind(this);
    this.changeStatusUser = this.changeStatusUser.bind(this);
    this.handleInputChange = this.handleInputChange.bind(this);
    }

    componentDidMount(){
        this.getUsers();
    }
    
    getUsers() {
        axios
        .get("http://localhost:3002/users")
        .then(res=>{
            this.setState({
                users : res.data
            })
            console.log(this.state.users);
        })
        .catch(err=>{
        console.log("error");

        })
    }

    // confirmDeleteUser(){

    // }
    
    deleteUser(e) {
        this.selectUser(e);
        axios
        .delete("http://localhost:3002/deleteUser",{ 
            data : {
                id : this.state.clickedUser
            }
        })
        .then(res => {
            if(res.data.error){
                this.setState({
                    Message : res.data.message
                })
            }else{
                this.setState({
                    Message : res.data.message
                })
            }
            this.getUsers();
            })
            .catch(err=>{
                console.log("error");
            })
        }
    //changeStatusUser 

    changeStatusUser() {
        const userStatus = {
            id : this.state.clickedUser,
            isAdmin : this.state.isAdmin
        }
        console.log("test" , this.state.isAdmin, this.state.clickedUser);
        if(this.state.isAdmin == 0 || this.state.isAdmin == 1) {
            axios
            .patch("http://localhost:3002/changestatususer",{userStatus})
            .then(res => {
                this.setState({
                    message : res.data.message
                })
                console.log(res.data.message);
            })
            .catch(err => {
                this.setState({
                    message : "Une erreur s'est produite veuillez reessayer"
                })
                console.log("error");
            })
        }else {
            this.setState({
                message : "Taper 1 pour admin, 0 pour user"
            })
        }
    }

    // select user clicked 

    selectUser(e) {
        this.setState({
            clickedUser : e.target.name
        }
        ,() => {
         return this.state.clickedUser;

         });
    }
    
    updateUser(e){
        this.selectUser(e);
        this.setState({
            UpdateClicked : true
        }, () => {
            console.log("clicked user" , this.state.clickedUser);
            console.log("updtate clicked" , this.state.UpdateClicked);
            console.log("sessionstorage id", sessionStorage.id);
            console.log("condition", !(this.state.UpdateClicked && sessionStorage.id == this.state.clickedUser) );
            return this.state.updateUser;
        });
       
    }

    handleInputChange(e) {
        this.setState({
          isAdmin : e.target.value
         }); 
         console.log(this.state.isAdmin);
       } 


    

   render() {

    
  




    return (
      
    <main>
        <h1> Dashboard Admin </h1>
        <h2>      {this.state.Message}           </h2>
        <p> {this.state.message} </p>

        
        <table id="tableau">
        <thead>
            <tr>
                <td>Id utilisateur </td>
                <td> Admin </td>
                <td> Nom user</td>
                <td> Prenom user</td>
                <td>Salle de sport</td>
                <td> Ville</td>
                <td> Pseudo </td>
                <td> Mail </td>
                <th> Modifier</th>
                <th> Envoyer </th>
                <th> Supprimer</th>
            </tr>
           </thead>
            {this.state.users.map((user,i) => 
            
            !(this.state.UpdateClicked && user.id_utilisateur == this.state.clickedUser) ?

                
             <tr>
               <td>{user.id_utilisateur} </td>
               <td> {user.is_admin} </td> 
                <td>{user.nom_user}</td>
                <td>{user.prenom_user}</td>
                <td>{user.salle_de_sport}</td>
                <td>{user.ville}</td>
                <td>{user.pseudo}</td>
                <td>{user.mail}</td> 
                <td><button onClick={this.updateUser} name={user.id_utilisateur} className="editbtn"> Modifier </button></td>
                <td><button className="sendbutton" name={user.id_utilisateur} disabled> Envoyer </button></td>
                <td><button onClick={this.deleteUser} name={user.id_utilisateur} className="deletebtn"> Supprimer </button></td>
            </tr> :

    <tr>
        <td>{user.id_utilisateur}</td>
        <td><input onChange={this.handleInputChange} placeholder={user.is_admin} /> </td>
        <td>{user.nom_user}</td>
        <td>{user.prenom_user}</td>
        <td>{user.salle_de_sport}</td>
        <td>{user.ville}</td>
        <td>{user.pseudo}</td>
        <td>{user.mail}</td> 
        <td><button onClick={this.updateUser} name={user.id_utilisateur} className="editbtn"> Modifier </button></td>
        <td><button onClick={this.changeStatusUser} className="sendbutton" name={user.id_utilisateur}> Envoyer </button></td>
        <td><button onClick={this.deleteUser} name={user.id_utilisateur} className="deletebtn"> Supprimer </button></td>
    </tr> 
     )}
  </table>
    </main>
    )

  }


}





export default DashboardAdmin;







