import React from 'react';
//import '../../../reset.css';
//import '../../../index.css';
import './profil.css';
import pp from  './../../../images/black-queen.jpg';
import axios from 'axios';
import MaterialIcon from 'material-icons-react';

class Profil extends React.Component {

  constructor() {
    super();
    
    this.state = {
      users : [],
      isEditable : false,
      nom : '',
      prenom : '' ,
      salle_de_sport : '',
      ville : '',
      email : '',
      password : '',
      newPassword : '',
      input : null,
      pp : null,
      Message : ''
    
    }

    this.getUserInfo = this.getUserInfo.bind(this);
    this.editprofil = this.editprofil.bind(this);
    this.canceledit = this.canceledit.bind(this);
    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.checkFileSize = this.checkFileSize.bind(this);
    this.checkFileType = this.checkFileType.bind(this);
    this.prepareFile = this.prepareFile.bind(this);
    this.uploadToServer = this.uploadToServer.bind(this);
    this.editmailprofil = this.editmailprofil.bind(this);
    this.deleteprofil = this.deleteprofil.bind(this);

  }

  componentDidMount(){
   this.getUserInfo();
    this.setState({
      input : document.querySelector("#input_file"),
   })
   if(sessionStorage.jwt) {
    console.log("je suis profil" ,sessionStorage.jwt)
    }else {
      console.log("no session");
    }
  }




  getUserInfo() {

    const user = {
      id: sessionStorage.id
    }

    axios
    .post(`http://localhost:3002/userlogged`, {user})
    .then(user => {
     this.setState({
          users : user.data,
      });
      console.log(this.state.pp);
    })
    .catch(err => {
      console.log("error"); 

    })
}

  
editprofil(){
  console.log("test");
  this.setState({
    isEditable : true
  })
  this.state.isEditable
}

handleInputChange(e) {
  this.setState({
     [e.target.name]: e.target.value
    });
    console.log(this.state.email);
}

handleSubmit(e) {
  const user = {
    nom : this.state.nom,
    prenom : this.state.prenom,
    salle_de_sport : this.state.salle_de_sport,
    ville : this.state.ville,
    id : sessionStorage.id
  }
 axios
  .patch(`http://localhost:3002/editprofil`, {user})
  .then(response => {
    //this.getUserInfo()
    console.log(response.data);
    })
  .catch(err => {
    console.log("error"); 
  })
}


canceledit(){
  this.setState({
    isEditable : false,
  })
}

editmailprofil() {
  const user  =  {
    mail : this.state.email,
    id : sessionStorage.id
  }
  console.log(this.state.email);

  axios
  .patch(`http://localhost:3002/editmailprofil`, {user})
  .then(response => {
    console.log(response.data);
    })
  .catch(err => {
    console.log("error"); 
  })
}

editpasswordprofil(){
    const user  =  {
      password : this.state.password,
      newPassword : this.state.newPassword,
      id : sessionStorage.id
    }
    console.log(this.state.newPassword);
    axios
    .patch(`http://localhost:3002/editmailprofil`, {user})
    .then(response => {
      console.log(response.data);
      })
    .catch(err => {
      console.log("error"); 
    })
  
}

deleteprofil(){
  axios
    .delete("http://localhost:3002/deleteprofil",{ 
            data : {
                id : sessionStorage.id
            }
        })
        .then(res => {
          if(res.data.error){
            this.setState({
                Message : res.data.message
            })
        }else{
            sessionStorage.clear();
        }
        console.log("this.state.Message");
        })
        .catch(err=>{
            console.log("error");
        })
}




checkFileType(type){
  //verifie que le fichier est une image
  const isAuthorized=[
    "image/gif",
    "image/jpeg",
    "image/png",
    "image/svg+xml"
  ].find(mime => {// chaque case du tableau est parcourue
  return mime === type;
  //si le type du fichier est trouve, retourne la case
  //sinon si aucune valeur ne correspond, retourne undefined
  });
  return Boolean(isAuthorized); //conversion en boolean
}

checkFileSize(size){
  // verifie que l'image ne depasse pas 2mo
  return (size / 1024) / 1024 <=2; 
}

prepareFile() {
  this.state.input.click();
  this.state.input.onchange = (e) =>{
    const file = e.target.files[0];
    if(!file) return;
    let warning = "";
      warning += !this.checkFileType(file.type) ? "type non supporte \n" : "";
      warning += !this.checkFileSize(file.size) ? "fichier trop lourd \n" : "";
      if(!warning){
        this.uploadToServer(file);
      }else{
        console.log("pas charger")
      }
    }
}

uploadToServer(file){
  const fd =  new FormData(); //multer a besoin d'un formadata()
  fd.append('image_upload', file);// le name image_upload sera utilise par multer
  fd.append('id', sessionStorage.id);
  console.log("----FORMDATA A ENVOYER VERS NODE----");
  console.log("ok" , fd);
  axios({//axios retourne une promise(promesse)
    method:"patch",
    url:"http://localhost:3002/avatar",
    data: fd,   
      
    onUploadProgress: function (evt){
        //Do whatever u want with the native progress event
        let percentLoaded = Math.floor((evt.loaded * 100) / evt.total);
        console.log(percentLoaded + "%");
        },
  }).then (res =>{// la promesse a ete tenue (pas d'erreur)
    this.setState({
      pp : res.data.newAvatar
    })
    console.log("uploader", this.state.pp, res.data.message );
  }).catch(err => {//promesse a ete rompu (erreur 40x || 50X)
    console.log(err);
  });
}

   render() {

    
  
  




    return (
      
    <div>
      <div id="bandeau"> </div>

      
      <div> 
      <div id="conteneur">

      
        {this.state.users.map( user => 
        <div id="bloc">
          <div className="infoperso test">
              {this.state.isEditable ? 
                <div className="inputgroup">                    
                    <input className="inputs" type="text" value={user.pseudo}/>
                    <input onChange={this.handleInputChange} className="inputs" type="text" name="prenom" placeholder="prenom"/>
                    <input onChange={this.handleInputChange} className="inputs" type="text" name="nom" placeholder="nom"/>
                    <input onChange={this.handleInputChange} className="inputs" type="text" name="salle_de_sport" placeholder="salle de sport"/> 
                    <input onChange={this.handleInputChange} className="inputs" type="text" name="ville" placeholder="ville"/> 
                 </div>
                  : 
                  <div className="inputgroup">
                    <h1>  {user.pseudo}</h1>
                    <p> {user.prenom_user} {user.nom_user}</p>
                    {user.salle_de_sport.length > 0 && user.ville.length > 0 ? 
                    <p> {user.salle_de_sport} , {user.ville} </p> :
                    <p>  {user.salle_de_sport}  {user.ville}  </p>
                    }
                  </div> 

                 
              }
       

          </div>
            
           
             
          
        </div>
      )}
       <div  id="btn">
             {
               this.state.isEditable ?
               <div id="ok">
               <a onClick={this.canceledit} className="editbutton"> <span>annuler</span>  <MaterialIcon   icon="cancel" /> </a>
               <a className="editbutton test" onClick={this.handleSubmit}> <span> Enregistrer les modifications </span> <MaterialIcon icon="save_alt" /> </a>
               </div> 
               : 
               <a onClick={this.editprofil} className="editbutton test"> <span> Editer le profil</span> <MaterialIcon  icon="create" />  </a>


             } 
            </div>
           
      <div onClick={this.prepareFile}>
          <input type="file" id="input_file" accept="image/*" />
          <i id="icon" class="fa fa-upload"></i>
      </div>

      </div>
      
        
         {this.state.users.map(user => 
          user.avatar !== null ? 
         (<img id="pp" src={require(`../../../../static/uploads/Avatar/${user.avatar}`)} /> ) : 
         this.state.pp !== null ? 
          <img id="pp" src={this.state.pp} /> :
          <img id="pp" src={pp} /> 

         )}
       

      </div>
      


    {this.state.users.map(user => 

      <div id="param">
       <div id="content">
       <div className="section">
          <h2> Paramètre </h2> 
          <h3> Votre e-mail </h3> 
          <p> Adresse e-mail actuel : <br />  <input className="inputs" type="text" value={user.mail} readonly/> </p> 
          <p> Nouvel adresse e-mail : <br /> <input onChange={this.handleInputChange} className="inputs" name="email" type="text" /></p> 
          <input onClick={this.editmailprofil} className="button" type="submit" value="envoyer" /> 
       </div> 

  {/*     <div className="section">
          <h3> Changer votre mot de passe  </h3> 
          <p> Ancien mot de passe : <input className="inputs" type="text" /> </p> 
          <p> Nouveau mot de passe : <input className="inputs" type="text" /></p> 
          <p> Réécrire nouveau mot de passe : <input className="inputs" type="text" /></p> 
          <input className="button"  type="submit" value="envoyer" />
       </div>
    */}

        <div className="section">
          <h3> Supprimer votre compte </h3>
          <input onClick={this.deleteprofil} className="deletebtn" type="submit" value="supprimer mon compte" /> 
          <p> {this.state.Message} </p>
        </div> 
       </div>



      </div>
    )}

    
      
    </div>
    )


  }



}





export default Profil;







