import React from 'react';
import '../../../reset.css';
import '../../../index.css';
import '../register/register.css';
import axios from 'axios';
import MaterialIcon from 'material-icons-react';
import createHistory from "history/createBrowserHistory";



class Signin extends React.Component {

  constructor() {
    super();
    this.state = {
      email : '',
      password : null,
      message : null,
     };


  
 
    
   this.handleInputChange = this.handleInputChange.bind(this);
   this.handleSubmit = this.handleSubmit.bind(this);
  }



  

  handleInputChange(e) {
     this.setState({
       [e.target.name]: e.target.value
      }); 
    } 
  

  handleSubmit(e) {
    e.preventDefault(); // emêche le comportement d'un formulaire (= rechargement de la page) 
    const user = {
      email : this.state.email,
      password : this.state.password
    } 

    axios({
      method : "post",
      url : `http://localhost:3002/Login`,
      data : {
        user : user
      },
      withCredentials: true    
    })
    .then(response => {
      this.setState({
        message : response.data.message
      })
      //localStorage.authentification = response.data.authentification;
      var token = response.data.token;
      console.log(token.newUser);
      sessionStorage.setItem('id', token.newUser.id);
      sessionStorage.setItem('isAdmin', token.newUser.is_admin);
      const history = createHistory();

      // Get the current location.
      const location = history.location;
      
      // Listen for changes to the current location.
      const unlisten = history.listen((location, action) => {
        // location is an object like window.location
        console.log("action" , action, "locpathname" ,  location.pathname);
      });
      
      // Use push, replace, and go to navigate around.
      history.push("/profil");  
      history.go();
    
      // To stop listening, call the function returned from listen().
      unlisten();
      

     
    })
    .catch(err => {
      console.log("data not send", err.response); 

    })


}
//   parseJwt (token) {
//   console.log("test du decodage");
//   var base64Url = sessionStorage.jwt.split('.')[1];
//   var base64 = base64Url.replace('-', '+').replace('_', '/');
//   var tok = JSON.parse(window.atob(base64));
//   console.log(tok.newUser);
// };







   render() {

  const  stylecss2 = {
    visibility :  this.props.stylecss,
  }
  console.log(stylecss2);




    return (
      
    <div  id="openModal" className="modalDialog"  style={stylecss2} stylecss='hidden'>
        <div className="modal">
          <a className="iconclear" title="Fermer" onClick={this.props.close}   >   <MaterialIcon icon="clear" /> </a>
          
          <div   id="register">
            <form onSubmit={this.handleSubmit} className="formlogin"> 
                <input onChange={this.handleInputChange}  className="inputregister"id="email" name="email" type="email" placeholder="E-mail"/> 
                <input onChange={this.handleInputChange}  className="inputregister"id="password" name="password"  type="password" placeholder="mot de passe "/>
                <input className="button" type="submit" value="SE CONNECTER"/> 
                <p> {this.state.message} </p>
                <a onClick={this.props.register}> Pas encore inscrit ? </a>

            </form>

          </div>
    </div>
  </div>

    );
  }


}





export default Signin;







