import React from 'react';
import '../../reset.css';
import '../../index.css';
import './banners.css';
import Register from "../pages/register/Register";


class BannerImg extends React.Component {


  constructor(props) {
    super(props);
    this.state = {
      open : false,
      closemodal : 'hidden',
    };

  }

  onOpenModal = () => {
    console.log("open");
    this.setState({ 
        open: true,
        closemodal : 'visible',
     });
    
    console.log("on open" , this.state.open);

  };

  onCloseModal = () => {
    console.log("cliqué");
    
    this.setState({ 
        open: false,
        closemodal : 'hidden',
    });
    console.log("on close" , this.state.open, this.state.closemodal);


    

  };
    render() {
      return (
        <div className="bannerimg" >
            <div className="bloctext">
              <h1 className="titlebannerimg" > Retrouvez en quelques cliques toutes les explications sur les exercices pour un entrainement réussi
              </h1>
              <h1 className="cta" onClick={this.onOpenModal}>Rejoignez-nous  </h1>
            </div>
            <Register   close={this.onCloseModal} stylecss={this.state.closemodal} signin={this.onOpenModalSignin} />
        </div>
    );
    }
  }




export default BannerImg;


