import React from 'react';
import '../../../reset.css';
import '../../../index.css';



class Exercice extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      exercice : this.props.location.state.exercice,
      machine : this.props.location.state.machine
    }


  }

  componentDidMount(){
    console.log("exercice" , this.state.exercice);
    console.log("machine", this.state.machine);
  }


    render() {
    
      return (
     <div>
        <h1 className="titlemachines"> {this.state.exercice.nom_exo} </h1>
          <div id="blocexercice"> 

            
            <div className="blocinfos">
              <div id="info">  
                  <h2> Infos</h2>
                  <ul>
                    <li> Zone : {this.state.exercice.partie} du corps  </li>
                    <hr /> 
                    <li> Muscles travaillés : 
                    {
                   this.state.exercice.muscles.map((muscle,i) =>
                   i < this.state.exercice.muscles.length -1  ?
                    <span> {muscle.nom}, </span> :
                    <span> {muscle.nom} </span>
                  )} 
                    </li> 
                    <hr />
                    {this.state.machine.map(noms => 
                      <li> Machine : {noms.nom_machine_en} ou {noms.nom_machine_fr} </li>
                      )}
                    
                    <hr /> 
                  </ul>
              </div>
            <p> {this.state.exercice.info}</p>
            </div>
            <div className="blocvideos">
                <iframe width="700" height="315" 
                src="https://www.youtube.com/embed/ic4b0qBRzGk" 
                frameborder="0" 
                allow="autoplay; encrypted-media" 
                allowfullscreen>
                </iframe>
                <a> Voir les réglages de la machine</a>
            </div> 

          </div>
     </div>
    );
    }
  } 


 

export default Exercice;


