import React from 'react';
import '../../../reset.css';
import '../../../index.css';
import './contact.css';



class Contact extends React.Component {
    render() {
      return (
     <div>
        <div className="headercontact">
            <div className="bloctextheader">
                <h1> Dites-nous tout ! </h1>
                <p> Vous avez une question ou une remarque ? <br /> Écrivez-nous ! !</p>
            </div> 
        </div>
        <form id="contactform" action="https://formspree.io/a-sophie.simon@live.fr" method="POST">
                <input className="inputregister" type="text" placeholder="nom" name="name" /> 
                <input className="inputregister" type="text" placeholder="prénom" name="firstname" />
                <input className="inputregister" type="email" placeholder="e-mail" name="_replyto" />
                <input className="inputregister" type="text" placeholder="sujet" name="object" /> 
                <textarea className="inputregister" type="text" placeholder="votre message" name="message" /> 
                 <input className="button" type="submit" value="ENVOYER" />
        </form>
     </div>
    );
    }
  } 


 

export default Contact;


