import React from 'react';
import '../../../reset.css';
import '../../../index.css';
import './gym.css';
import {Link} from 'react-router-dom';
import axios from 'axios';

class GuidePage extends React.Component {

  constructor() {
    super();
    this.state = {
      exercices : [],
      images : [],
      machine : []
    }

    this.getMachines = this.getMachines.bind(this);
    this.getImages = this.getImages.bind(this);

  }

  componentDidMount(){
    const clickedtest  = this.props.location.state.clickedtest;

      if(clickedtest) {
        this.getMachines();
    }
  }




  getMachines() {


     var machineRequest = {
      machine : this.props.location.state.machine,
      typeMachine : this.props.location.state.typeMachine
    }

    console.log("machine request", machineRequest);

    axios
    .post(`http://localhost:3002/machines`, {machineRequest})
    .then(machines => {
     // const exercices = exercice.data
      console.log("rep", machines.data);
     this.setState({
          exercices : machines.data.exercices,
          machine : machines.data.machine
      });
      console.log("res machine", this.state.exercices); 
      console.log("machine", this.state.machine);
    })
    .catch(err => {
      console.log("error"); 

    })
}

getImages() {
  console.log("get click");
  axios
   .get(`http://localhost:3002/images`)
   .then(image => {
    const data = image.data
    var tab = [];

    data.forEach(function(d) {
      tab.push(JSON.parse(d.test));
    });




    this.setState({
         images : tab
     }); 
     console.log("image", this.state.images);
   })
   .catch(err => {
     console.log("error");    
    })
}


    render() {
      return (
     <div className="mainmachines">
       <h1 className="titlemachines"> Resultats pour {this.props.location.state.machine} </h1> 

       <div  className="content"> 

      

       {this.state.exercices.map((exercice, i)  => 
        
                        


      <div   className="machines">
              
                
              
              <img  alt="legpress" className="imagestyle"  src={require(`../../../images/exercices/${exercice.url_thumbnail}`)}   />
              <h2> {exercice.nom_exo} </h2>

              <p>  
                {exercice.partie} du corps : 

                 {
                   exercice.muscles.map((muscle,i) =>
                   i < exercice.muscles.length -1  ?
                    <span> {muscle.nom}, </span> :
                    <span> {muscle.nom} </span>
                  )} 
                
              
              </p>
              
              
      
              <Link to={{
                pathname : "/exercice",
                state : {
                  exercice : exercice,
                  machine : this.state.machine
                }
              }}>
                <button className="buttonguide"> voir exercice </button> 
              </Link> 
      </div>
)}    



       



       

       </div>

         <button onClick={this.getImages}> test </button> 
         <button > parse jwt </button> 

            {this.state.images.map(image => 
        
                        


        <div>
                
                  
                
              
                <p> {image.image} : {image.image2} </p>
                
  
        </div>
  )}    
  



          
      
    
       




     </div>
    );
    }
  } 




export default GuidePage;


