import React from 'react';
import {Link} from 'react-router-dom';
import '../../reset.css';
import './navigation.css';
import logo from '../../images/logo.png';
import Register from '../pages/register/Register';
import Signin from '../pages/signin/Signin';
import createHistory from "history/createBrowserHistory";
import axios from 'axios';

class Navigation extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
          open : false,
          openSignin : false,
          closemodal : 'hidden',
          closemodalSignin : 'hidden'
        };

      }

      onOpenModal = () => {
        console.log("open");
        this.setState({ 
            open: true,
            closemodal : 'visible',
         });
        
        console.log("on open" , this.state.open);

      };

      onCloseModal = () => {
        console.log("cliqué");
        
        this.setState({ 
            open: false,
            closemodal : 'hidden',
        });
        console.log("on close" , this.state.open, this.state.closemodal);
    
    
        

      };

      onOpenModalSignin = () => {
        console.log("open");
        this.setState({ 
            openSignin: true,
            closemodalSignin : 'visible',
         });
        console.log("on open" , this.state.open);

      };

      onCloseModalSignin = () => {
        console.log("cliqué");
        
        this.setState({ 
            openSignin: false,
            closemodalSignin : 'hidden',
        });
        console.log("on close" , this.state.open, this.state.closemodalSignin);
        };

        logout() {
           
            console.log("logout");
            
        axios
            .get(`http://localhost:3002/logout`, sessionStorage.id)
            .then(response => {
            console.log(response.data.token);
            sessionStorage.clear();
            const history = createHistory();

            // Get the current location.
            const location = history.location;
            
            // Listen for changes to the current location.
            const unlisten = history.listen((location, action) => {
              // location is an object like window.location
              console.log("action" , action, "locpathname" ,  location.pathname);
            });
            
            // Use push, replace, and go to navigate around.
            history.push("/home");
            history.go();
            
            
            // To stop listening, call the function returned from listen().
            unlisten();

            
            
            })
            .catch(err => {
            console.log("error"); 
            })
        }
      
    
    
      
      
    
 render() {


    



        return (

             
             sessionStorage.isAdmin == 0 ? 

        
               
               (
                <div className="main">
                <nav className="nav">  
                    <div className="logo navitems">
                         <Link to="/">  <img src={logo} alt="neobuild-logo" width="70"/> </Link> 
                    </div> 
                    <div className="menu navitems dropdown">
                        <p className="profil"> Mon compte </p>
                            <ul className="menuDeroulant">
                                <li><Link to="/profil"> Mon profil</Link></li>
                                <li><a  onClick={this.logout}>Se déconnecter</a> </li>
                            </ul>
                    

                    </div>
                </nav>
            
                
               
                </div>
                ): 
            sessionStorage.isAdmin == 1 ? 
            (
                <div className="main">
                <nav className="nav">  
                    <div className="logo navitems">
                         <Link to="/">  <img src={logo} alt="neobuild-logo" width="70"/> </Link> 
                    </div> 
                    <div className="menu navitems dropdown">
                        <p className="profil"> Mon compte </p>
                            <ul className="menuDeroulant">
                                <li><Link to="/profil"> Mon profil</Link></li>
                                <li><Link to="/dashboardAdmin"> Gestion des utilisateurs </Link></li>
                                <li><Link to="/dashboardMachines"> Gestion des machines </Link></li>
                                <li><a onClick={this.logout}>Se déconnecter</a> </li>
                            </ul>
                    

                    </div>
                </nav>
            
                
               
                </div>

            ) : 
            ( 
                <div className="main">
                <nav className="nav">  
                    <div className="logo navitems">
                         <Link to="/home">  <img src={logo} alt="neobuild-logo" width="70"/> </Link> 
                    </div> 
                    <ul className="menu navitems">
                        <li> <a onClick={this.onOpenModal}> M'inscrire </a> </li>
                        <li>  <a onClick={this.onOpenModalSignin}> Me connecter  </a></li>
                    </ul>
                </nav>
                <Register   close={this.onCloseModal} stylecss={this.state.closemodal} signin={this.onOpenModalSignin} />
                <Signin   close={this.onCloseModalSignin} stylecss={this.state.closemodalSignin} register={this.onOpenModal}  />

                
               
                </div>
            )
            
            

           

    

        



        );

    }

}
export default Navigation;


