import React from 'react';
import '../../reset.css';
import '../../index.css';
import './search.css';
import {Link} from 'react-router-dom';



class Search extends React.Component {

    constructor() {
        super();
        this.state = {
            machine : '',
            typeMachine : ''

        }
    this.handleInputChange = this.handleInputChange.bind(this);

    }
    handleInputChange(e) {
        this.setState({
            [e.target.name] : e.target.value
                 }); 

         console.log("machine", this.state.machine, "type", this.state.typeMachine)
         
    }
      
    



render() {

return (
<div>
        <div>
            
                <div>
                <header>
                <h1 style={h1}> JE RECHERCHE UNE MACHINE </h1>
                <div className="input">
                    <input onChange={this.handleInputChange} className="items inputstyle" name="machine" type="text" placeholder="Leg press" />
                    <form className="items inputstyle">
                        <div>
                        <input onChange={this.handleInputChange} type="radio" name="typeMachine" value="assisté" id="assiste"/>
                        <label htmlFor="assiste"> Assistée</label>
                        </div>
                        <div>    
                        <input onChange={this.handleInputChange} type="radio" name="typeMachine" value="poids libre" id="poidslibre"/>
                        <label htmlFor="poidslibre">Poids libre</label>
                        </div>
                    </form>
                    <div>
                        <Link style={test} to={{
                                pathname :  "/guidepage",
                                state : {
                                    clickedtest : true,
                                    machine : this.state.machine,
                                    typeMachine : this.state.typeMachine
                                }
                        }}>
                            <div  className="button items"> RECHERCHER </div>
                        </Link>

                    </div>
                   {/* <a className="link items">Je ne connais pas le nom de la machine ? </a> */}
                </div>
            </header>
            </div>


                    

        





</div>
</div>
);



    }
  }




  const h1 = {
    textAlign: 'center',
    fontFamily : 'Helvetica',
    paddingTop: 50,
    fontSize : 30,
    color:'#FFF'
}

const test = {
    textDecoration : 'none' 
}

export default Search;


