import React from 'react';
import '../../../reset.css';
import '../../../index.css';
import './dashboardMachines.css';

import axios from 'axios';


class DashboardMachines extends React.Component {

constructor() {
    super();
    this.state = {
        machines : [],
       
    }
    this.getMachines = this.getMachines.bind(this);
}

componentDidMount() {
    this.getMachines();
}

  

    getMachines() {
        axios
        .get("http://localhost:3002/machinesexosmuscles")
        .then(res=>{
            this.setState({
                machines : res.data
            })
            console.log(this.state.machines);
        })
        .catch(err=>{
        console.log("error get machines");

        })
    }


   

    

   render() {

    
  




    return (
      
    <main>
       <h1> Dashboard Machines</h1>
    </main>
    )

  }


}





export default DashboardMachines;







